import React from 'react';
import { useSelector, useDispatch } from "react-redux";

import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars } from '@fortawesome/free-solid-svg-icons';
import styled from 'styled-components';


function Menubar() {
    // dispatchë¥¼ ì‚¬ìš©í•˜ê¸° ìœ„í•œ ì¤€ë¹„
    const dispatch = useDispatch();

    // isMenuê°€ í•¨ìˆ˜ì»´í¬ë„ŒíŠ¸ ì•ˆì— ìˆì–´ì„œ ì „ì—­ì˜ styled-componentê°€ ì‚¬ìš©í•˜ì§€ ëª»í•˜ëŠ” ì¤‘ì„
    const _isMenu = useSelector(state => {
        return state.reducerMenu.menu;
    });

    return (
        <StateBar >
            <StateBarColumn style={{ paddingTop: '5px' }}>
                <FontAwesomeIcon icon={faBars}
                    size="2x"
                    onClick={() => {
                        if (!_isMenu) {
                            dispatch({ type: 'OPEN_MENU' })
                        } else {
                            dispatch({ type: 'CLOSE_MENU' })
                        }
                    }} /></StateBarColumn>
            <StateBarColumn
                onClick={() => {
                    dispatch({ type: 'CLOSE_MENU' });
                    return;
                }}
            ><Link to='/' style={{ textDecoration: 'none', color: 'black', fontSize: '35px' }}
            >Hachaerin</Link></StateBarColumn>

        </StateBar>
    )
}


